import { graphql, Link, useStaticQuery, navigate } from "gatsby"
import { globalHistory } from "@reach/router"
import PropTypes from "prop-types"
import React from "react"

const langHandler = (e) => {
  e.preventDefault()

  // change current url
  let url = globalHistory.location.pathname.split("/")
  let idx = 1;
  if (globalHistory.location.pathname.includes("northBits")) {
    idx = 2;
  }
  console.log('url a', url);
  url[idx] = e.target.dataset.lang
  console.log('url b',url);
}

const Header = () => {
  const idx = globalHistory.location.pathname.includes("northBits") ? 2 : 1
  const currentLang = globalHistory.location.pathname.split("/", 3)[idx]
  const data = useStaticQuery(graphql`
    query {
     allUrlsJson {
        edges {
          node {
            id
            name
            path
            lang
          }
        }
      }
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  return (
    <header>
      <div className={"container"}>
        <nav>
          <ul className={"nav-menu"}>
            {data.allUrlsJson.edges
              .filter((el) => el.node.lang === currentLang)
              .map(({ node: { id, name, path } }) => (
                <li key={id}>
                  <Link className={"nav-menu__item"}
                        to={path}
                        activeClassName="active">{name}</Link>
                </li>
              ))}
          </ul>
          <ul className={"nav-menu lang"}>
            <li key={"eng"}>
              <a
                 data-lang={"en"}
                 onClick={e => langHandler(e)}
                 className={"nav-menu__item"}>En</a>
            </li>
            <li key={"heb"}>
              <a
                 data-lang={"he"}
                 onClick={e => langHandler(e)}
                 className={"nav-menu__item"}>He</a>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
